body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App_logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App_header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App_link {
  color: #61dafb;
}

.btn_back{
  background-color: white;
  color : #777;
  width : 70px;
  height : 30px;
  padding : 3px;
  text-align: center;
  text-transform: uppercase;
  display: block;
  position: absolute;
  left: 35px;
  top : 80px;
  border : none;
  outline : none;
}

.btn_add{
  background-color: #5cb85c;
  color : white;
  width : 70px;
  height : 30px;
  padding : 3px;
  text-align: center;
  text-transform: uppercase;
  display: block;
  position: absolute;
  left: 35px;
  top : 80px;
  border : none;
  outline : none;
}


.header {
    background-color: black;
    position : absolute;
    top : 0;
    left : 0;
    width : 100%;
    height : 60px;
}

.header__heading {
    color : white;
    letter-spacing: .3px;
}

.form{
    position: absolute;
    left:0px;
    top: 150px;
    width: 30%;
    height : 60vh;
    padding : 10px;
    text-align: left;
}

.form__input--name{
    display: block;
    margin-left: 10px; 
    width : 80%;
    height : 24px;
    outline : none;
    border : 1px solid #ddd;
    padding : 8px;
    text-decoration: none;
    border-radius : 4px;
}

.form__input__label--name{
    text-align : left;
    display: block;
    margin-left: 10px;
    color : #777;
}

.form__input--number{
    display: block;
    margin-left: 10px; 
    width : 80%;
    height : 24px;
    outline : none;
    border : 1px solid #ddd;
    padding : 8px;
    text-decoration: none;
    border-radius : 4px;
}

.form__input__label--number{
    text-align : left;
    display: block;
    margin-left: 10px;
    color : #777;
}

.form__subscriber{
    display: block;
    
}

.form__subscriber--title{
    color : #5bc0de;
}

.form__subscriber--name{
    
    color : #777;
}

.form__subscriber--name--content{
    color : #333;
}

.form__subscriber--number{
    
    color : #777;
}

.form__subscriber--number--content{
    color : #333;
}

.form__btn--add{
    background-color: #5cb85c;
    color : white;
    text-align: center;
    
    text-decoration: none;
    height : 30px;
    width : 65px;
    border : none;
    outline : none;
    text-transform: uppercase;
    

}

.list{
    position: absolute;
    top:120px;
    left:15px;
    padding : 10px;
    width: 65%;
    text-align: left;
}

.list__indv{
    margin-top : 20px;
}

.list__name{
    
    display: inline-block;
    width : 15%;
    margin-right : 20%;
    margin-left : 0;
}
.list__number{
    display: inline-block;
    width : 15%;
    margin-left: 20%;
    margin-right: 20%;
}

.list__delete{
    background-color: 	#d9534f;
    color: white;
    height : 35px;
    width : 70px;
    padding : 4px;
    border: none;
    outline : none;
    border-radius: 2px;
}
